<template>
  <div class="card-item" @click="catDetail">
      <div class="img" >
        <img :src="card.imgurl || '/static/img/default-bg.jpg'" alt="">
        <div v-if="card.overdue" class="overdue">失效</div>
      </div>
     <div class="infos">
        <h5>{{card.name}}</h5>
        <p>有效时间: {{card.end_time}}</p>
        <p>优惠码：{{card.token}}</p>
    </div>
    <div v-if="!card.overdue" class="zengsong" @click="away">赠送</div>
  </div>
</template>

<script>
import bus from '../../../assets/js/bus.js'
export default {
  name: 'Card-Item',
  props: {
    card: Object
  },
  methods: {
    catDetail () {
      bus.$emit('selectOneCard', this.card)
    },
    away (e) {
      e.stopPropagation()
      bus.$emit('giveAway', this.card)
//      this.$parent.$emit('giveAway', this.card)
    }
  }
}
</script>

<style lang="less" scoped>
.card-item{
    display: flex;
    align-items: center;
    padding: .5rem .4rem;
    background-color: #fff;
    margin-top: .6rem;
    .img {
        width: 4.6rem;
        height: 4.6rem;
        position: relative;
        img{
            width: 100%;
            height: 100%;
        }
    }
    .infos {
        flex: 1;
        h5 {
            padding-left: 1rem;
            margin-top: 0;
            margin-bottom: .4rem;
        }
        p {
            margin: 0;
            padding-left: 1rem;
            font-size: 13px;
            color:#999;
        }
    }
}
.overdue{
  position: absolute;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  line-height: 4.6rem;
  color: #fff;
  background: rgba(0, 0, 0, .7);
}
  .zengsong{
    display: block;
    padding: 2rem;
  }
</style>



