<template>
  <div class="wrap">
    <app-header
    title="卡券详情"
    showBack="false"
    close="true"
    ></app-header>
    <div class="main">
      <div class="img">
        <img :src="card.imgurl" alt="">
        <div v-if="card.overdue" class="overdue">失效</div>
      </div>
      <div class="detail">
        <div class="item" v-if="!card.overdue">
          <div class="title">意向商户</div>
          <div class="content" style="font-size:14px;">
            <select name="" id="" v-model="selected">
              <option
                  v-for="(item, index) in card.allow_unit"
                  :key="index"
                  :value="item.id"
              >{{item.name}}</option>
            </select>
          </div>
        </div>
        <div class="item">
          <div class="title">使用规则</div>
          <div class="content" style="font-size:14px;" v-html="card.description"></div>
        </div>
        <div class="item">
          <div class="title">适用商品</div>
          <div class="content">
            <div class="goods-item"
             v-for="(item, index) in card.allow"
             :key="index"
             @click="goTo(item.mall_id)"
            >
              <div class="img"><img :src="item.imgurl || '/static/img/default-bg.jpg'" alt=""></div>
              <div class="info">{{item.name}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header'
import {setMaintain} from '../../../api/home'
export default {
  name: 'Card-Detail',
  props: {
    card: Object
  },
  data () {
    return {
      selected: ''
    }
  },
  watch: {
    selected: function (val) {
      if (val !== undefined) {
        setMaintain(this.selected, this.card.id)
          .then(data => {
            if (data.state) {
              this.$toast(data.errormsg || '设置成功')
            } else {
              this.$toast(data.errormsg || '设置失败')
            }
          })
          .catch(err => {
            console.log(err)
            this.$toast('网络错误')
          })
      }
    }
  },
  methods: {
    goTo (id) {
      this.$router.replace('/mall/item/' + id)
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
.wrap {
  .main {
    padding-top: 50px;
    background-color: #fff;
    .img {
      position: relative;
      // padding-top: 1rem;
      text-align: center;
      img {
        width: 85%;
        height: 230px;
        border-radius: 8px;
      }
    }
    .detail {
      .item {
        margin: 1rem;
        .title {
          background: #fafafa;
          padding: 6px 15px;
          border-bottom-color: #eee;
          color: #777;
          border-color: #ddd;
          border-bottom: 1px solid transparent;
          border-top-left-radius: 3px;
          border-top-right-radius: 3px;
        }
        .content {
          padding: .5rem 0;
          .goods-item {
            display: flex;
            padding: .5rem 0;
            .img {
              width: 3.5rem;
              img {
                width: 100%;
                height: 100%;
              }
            }
            .info {
              flex: 1;
              padding-left: 1rem;
              display: flex;
              align-items: center;
            }
          }
        }
      }
    }
  }
}
.overdue{
  position: absolute;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
  font-size: 22px;
  text-align: center;
  line-height: 230px;
  color: #fff;
  background: rgba(0, 0, 0, .7);
}
</style>



