<template>
    <div class="ui-wrap">
        <app-header
        title="我的卡券"
        showBack="true"
        ></app-header>
        <div class="card-wrap">
            <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
                <card-item
                    v-for="(item, index) in cardList"
                    :key="index"
                    :card="item"
                ></card-item>
            </mt-loadmore>
        </div>
        <mt-popup
         style="height: 100%; width: 100%; z-index: 99999;overflow-y: scroll;"
          popup-transition="popup-fade"
          v-model="popupVisible"
          position="left">
          <card-detail
          :card="cardDetail"
          ></card-detail>
        </mt-popup>
    </div>
</template>

<script>
import AppHeader from '../../../components/header/header'
import CardItem from './card-item'
import CardDetail from './card-detail'
import {getUserCard, giveCoupon} from '../../../api/home.js'
import bus from '../../../assets/js/bus.js'
export default {
  name: 'Card',
  data () {
    return {
      cardList: [],
      popupVisible: false,
      cardDetail: '',
      page: 0,
      allLoaded: false
    }
  },
  mounted () {
    bus.$on('selectOneCard', card => {
      this.popupVisible = true
      this.cardDetail = card
    })
    // 赠送卡券
    bus.$on('giveAway', card => {
      this.$messagebox.prompt('请输入要赠送的人的手机号或账号', '赠送卡券')
        .then(({value, action}) => {
          if (!value) return this.$toast('请输入要赠送的人的手机号或账号')
          giveCoupon(card.token, value)
            .then(data => {
              if (data.state) {
                this.$toast(data.errormsg || '赠送成功')
                this.getDatas()
              } else {
                this.$toast(data.errormsg || '赠送失败')
              }
            })
        })
        .catch(() => {
        })
    })
    // 关闭modal box
    bus.$on('clickCloseModal', () => {
      this.popupVisible = false
    })
    this.getDatas()
  },
  methods: {
    getDatas () {
      this.$indicator.open({
        text: '加载中'
      })
      this.$set(this, 'page', (this.page + 1))
      getUserCard(this.page).then(data => {
        if (data.state && data.data.list) {
          this.cardList.push(...data.data.list)
          this.$indicator.close()
          if ((this.page * 18) > data.page) {
            this.allLoaded = true
          }
          if (data.data.list.length === 0) {
            this.allLoaded = true
          }
        } else {
          this.$toast(data.errormsg || '暂无数据')
        }
      })
    },
    loadBottom () {
      this.getDatas()
      this.$refs.loadmore.onBottomLoaded()
    }
  },
  components: {
    AppHeader,
    CardItem,
    CardDetail
  }
}
</script>
<style lang="less" scoped>
    .ui-wrap:after{
        display: block;
        width: 100%;
        margin-top: 1rem;
        font-size: .8rem;
        content: '';
        border-bottom: 1px solid #ddd;
        color: #999;
        text-align: center;
    }
  .ui-wrap{
    height: 100%;
    overflow: hidden;
    margin-bottom: 0;
    padding-bottom: 55px;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
</style>



